import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import style from '../css/index.less';
import { addTodo, completeTodo, setVisibilityFilter, VisibilityFilters } from '../store/actions';

class App extends Component {

  componentDidMount() {
  		const { dispatch } = this.props;
  		let n = 0;
  		// setInterval(function(){
  		// 	n++;
  		// 	dispatch(completeTodo(n))
  		// },1000);
  	}	

  getValue(dispatch,event){
  	dispatch(addTodo(this.refs.text.value));
  }


  render() {
    const { dispatch,title,singer,lryic,m4a,n,lists } = this.props
    return (
      <div className={style.bor}>
      		<div className={style.pos}>
      		<input  type='text' ref='text'/>
      		<input type='button' value='查找' onClick={this.getValue.bind(this,dispatch)}/>
 			</div>
 			<div className={style.mar}>
 			<h3 className={style.h3}><p className={style.p}>{title}</p><p className={style.p1}>{singer?'|':''} {singer}</p></h3>
 			<ul className={style.ul}>
 				{
 					lryic?lryic.map(function(item,index){
 						return <li key={index}>{item}</li>
 					}):''
 				}
 			</ul>
 			</div>
 			<audio src={m4a} controls="controls" className={style.video_style}>
				您的浏览器不支持 video 标签。
			</audio>
      </div>
    )
  }
}

function select(state){
	return {
		title:state.todos.title,
		singer:state.todos.singer,
		lryic:state.todos.lryic,
		m4a:state.todos.m4a,
		n:state.todos.n
	}
}

export default connect(select)(App)


